@charset "UTF-8";
/* DEV — affichage du contour des blocs */
html, body {
  height: 100%;
}

.accueil {
  background-image: url("../img/accueil.jpg");
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}

.barrnav {
  color: #000;
}

.barrnav a {
  color: inherit;
}

.bienvenue {
  display: flex;
  align-items: center;
  height: 88px;
  margin-left: 28px;
  background-color: #FFFD;
}

.bienvenue h1 {
  margin: 0;
}

.hamb, .checkMenu {
  width: 60px;
  height: 60px;
  position: fixed;
  top: 14px;
  right: 28px;
}

.checkMenu {
  opacity: 0;
}

ul.menu {
  display: none;
  list-style: none;
  flex-direction: column;
  justify-content: space-between;
  background-color: #FFFD;
  position: fixed;
  right: 0;
  margin: 0;
  padding: 0;
}

ul.menu li {
  margin: 10px 0;
  min-width: 116px;
  text-align: center;
}

input[type='checkbox']:checked ~ ul.menu {
  display: flex;
}

@media screen and (min-width: 768px) {
  .hamb, .checkMenu {
    display: none;
  }
  ul.menu {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    width: initial;
  }
  .barrnav {
    display: flex;
    align-items: center;
    margin: 0 auto;
    background-color: #FFFD;
  }
  .bienvenue, ul.menu {
    background: initial;
  }
}
